import { Arr, Obj, Any, Dom } from "nano-js";

export default {

    name: 'NFormItem',

    inject: {

        NForm: {
            default: undefined
        },

        NTabs: {
            default: undefined
        },

        NTabsItem: {
            default: undefined
        }

    },

    props: {

        prop: {
            default()
            {
                return 'id';
            },
            type: [String]
        },

        label: {
            default()
            {
                return '';
            },
            type: [String]
        },

        tooltip: {
            default()
            {
                return '';
            },
            type: [String]
        },

        tooltipPosition: {
            default()
            {
                return 'right-center';
            },
            type: [String]
        },

        tooltipWindow: {
            default()
            {
                return false;
            },
            type: [Boolean]
        },

    },

    methods: {

        focusInput()
        {
            let $input = Dom.find(this.$el).find('input');

            if ( $input.empty() ) {
                return;
            }

            $input.get(0).focus();
        },

        gotoInput()
        {
            let errors = this.NForm.errors;

            if ( Any.isEmpty(errors) ) {
                return;
            }

            if ( ! this.NTabs || ! this.NTabsItem ) {
                return;
            }

            let keys = Any.keys(errors);

            if ( Arr.first(keys) !== this.prop ) {
                return;
            }

            this.NTabs.changeTab(this.NTabsItem.name);
        }

    },

    beforeMount()
    {
        this.NForm.addItem(this);
    },

    mounted()
    {
        this.NForm.$on('errors', this.gotoInput);
    },

    beforeDestroy()
    {
        this.NForm.removeItem(this);
    },

    renderTooltip()
    {
        if ( ! this.tooltip && ! this.$slots.tooltip ) {
            return null;
        }

        let props = {
            position: this.tooltipPosition,
            window: this.tooltipWindow,
            contain: this.tooltipWindow
        };

        return (
            <NPopover type="tooltip" props={props}>
                { this.$slots.tooltip || this.tooltip }
            </NPopover>
        );
    },

    renderLabel()
    {
        if ( ! this.label && ! this.$slots.label ) {
            return null;
        }

        return (
            <div class="n-form-item__label">
                <label vOn:click={this.focusInput}>
                    { this.$slots.label || this.label }
                </label>
                { this.ctor('renderTooltip')() }
            </div>
        )
    },

    renderError()
    {
        if ( ! Obj.has(this.NForm.errors, this.prop) ) {
            return null;
        }

        return (
            <div class="n-form-item__error">
                { Obj.get(this.NForm.errors, this.prop) }
            </div>
        );
    },

    render($render)
    {
        this.$render = $render;

        return <div class="n-form-item">
            { this.ctor('renderLabel')() }
            <div class="n-form-item__input">
                { this.$slots.default }
            </div>
            { this.ctor('renderError')() }
        </div>;
    }
}
