import { Link as LinkSource } from 'tiptap-extensions'

export default class Link extends LinkSource {

    get schema() {
        return {
            attrs: {
                href: {
                    default: null,
                },
                target: {
                    default: null,
                },
            },
            inclusive: false,
            parseDOM: [
                {
                    tag: 'a[href]',
                    getAttrs: dom => ({
                        href: dom.getAttribute('href'),
                        target: dom.getAttribute('target'),
                    }),
                },
            ],
            toDOM: node => ['a', node.attrs],
        }
    }

}